/* Color Theme Swatches in Hex */
$Chalk-lettering-1-hex: #BF907E;
$Chalk-lettering-2-hex: #A63117;
$Chalk-lettering-3-hex: #F2F2F2;
$Chalk-lettering-4-hex: #595959;
$Chalk-lettering-5-hex: #0D0D0D;

main.main {
    overflow: auto;
    display: flex;
    background-color: #fff;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    width: 100%;

    .main-page {
        width: 100%;
    }
}

::-webkit-scrollbar {
    width: 0px;
    height: 0px;
}